.Modal {
    position: fixed;
    z-index: 500;
    background-color: white;
    width: 70%;
    /* border: 1px solid #ccc; */
    /* box-shadow: 1px 1px 1px black; */
    /* padding: 16px; */
    left: 15%;
    top: 30%;
    box-sizing: border-box;
    transition: all 0.4s ease-out;
}

@media (min-width: 600px) {
    .Modal {
        width: auto;
        left: calc(50% - 250px);
    }
}